/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */
import {
	useBlockProps,
	InspectorControls,
	RichText,
} from '@wordpress/block-editor';
import { PanelBody } from '@wordpress/components';
import { useSelect } from '@wordpress/data';
import { ServerSideRender } from '@wordpress/editor';
import { useState } from '@wordpress/element';

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import './editor.scss';

export default function Edit({ attributes, setAttributes }) {
	// Collect attributes
	const { movieTitle } = attributes;

	// State to store data
	const [searchText, setSearchText] = useState('');
	const [allPosts, setAllPosts] = useState([]);

	// Set State Based on Search Result
	useSelect(
		(select) => {
			const posts = select('core').getEntityRecords(
				'postType',
				'rt-movie',
				{
					search: searchText,
				}
			);
			if (posts && posts?.length > 0) {
				setAllPosts(posts); // Set all posts in state based on search result
			}
		},
		[searchText]
	);

	// Autocomplete for search movie post
	const autoComplete = [
		{
			name: 'Autocomplete',
			triggerPrefix: '',
			options: allPosts,
			isDebounced: true,

			// Returns a label for an option
			getOptionLabel: (option) => option?.title.rendered,
			// Declares that options should be matched by their name
			getOptionKeywords: (option) => [option?.title.rendered],
			// Declares completions should be inserted as abbreviations
			getOptionCompletion: (option) => {
				setAttributes({
					moviePostID: parseInt(option.id),
					movieTitle: option?.title.rendered,
				});
			},
		},
	];

	return (
		<>
			<div {...useBlockProps()}>
				{/* This component fetches and renders server-side output for a specified block type. */}
				<ServerSideRender
					block="movie/rt-movie-block"
					attributes={attributes}
				/>
			</div>
			<InspectorControls>
				<PanelBody title={__('Search Movie Post')}>
					{/* Display Search box */}
					<RichText
						autocompleters={autoComplete}
						value={movieTitle}
						onChange={(value) => {
							setSearchText(value);
						}}
						placeholder={__('Search Here', 'movie-library')}
					/>
				</PanelBody>
			</InspectorControls>
		</>
	);
}
